@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@300;400;700&display=swap);
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  box-sizing: border-box;
}

ul {
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

ul li {
  list-style-type: none;
  display: block;
}

:root {
  --global-color-background: rgb(240, 231, 203);
  --global-color-grey-light: rgb(165, 165, 165);
  --global-color-grey-dark: rgb(84, 91, 94);
}

p {
  line-height: 1.5rem;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-weight: 400;
}

.font-large {
  font-size: 20px;
  line-height: 25px;
}

.font-regular {
  font-size: 16px;
}

.font-small {
  font-size: 14px;
}

.font-micro {
  font-size: 12px;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: "Kumbh Sans", sans-serif;
  height: 100%;
  width: 100%;
}

input {
  font-family: "Kumbh Sans", sans-serif;
}

.App {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  background-color: rgb(240, 231, 203);
  background-color: var(--global-color-background);
}

.content-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  width: 70%;
  height: 100vh;
  position: relative;
}

.video-container {
  width: 100%;
  height: 100%;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
}

.video-container video {
  background-position: center;
  background-size: contain;
  width: 100%;
  height: 100%;
  object-fit: fill;
}

.video-container .background-image {
  background: url(//robertasliekis.github.io/zolynu-paslaptys/static/media/video_screenshot.f8a06eb1.png);
  background-position: center;
  background-size: 100% 100%;
  height: 100%;
}

@-webkit-keyframes video-container-animation {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes video-container-animation {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

.info-containers {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  flex-direction: column;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
}

.info-containers .container-zones {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  z-index: 2;
}

.info-containers .container-zones .container {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 50%;
}

.info-containers .container-zones .container .plant {
  width: 50%;
  height: 100%;
  transition: 0.5s all ease;
  cursor: pointer;
  border: 3px solid transparent;
}

.info-containers .container-zones .container .plant:hover {
  border: 6px solid white;
}

.info-containers .plant-info-container {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  background-color: rgba(255, 255, 255, 0.5);
  transition: 0.6s all ease;
}

.info-containers .plant-info-container .plant-info-window {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  position: relative;
  align-items: flex-start;
  width: 80%;
  height: 80%;
  background-color: rgb(240, 231, 203);
  background-color: var(--global-color-background);
  border: 2px solid rgb(165, 165, 165);
  border: 2px solid var(--global-color-grey-light);
  padding: 2rem;
}

.info-containers .plant-info-container .plant-info-window .btn-close {
  position: absolute;
  right: 2rem;
  top: 2rem;
  width: 3rem;
  height: 3rem;
  border: 3px solid grey;
  cursor: pointer;
}

.info-containers .plant-info-container .plant-info-window .btn-close .line-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.info-containers .plant-info-container .plant-info-window .btn-close .line-container .line {
  position: absolute;
  transform: translate(-50%, -50%) rotate(-45deg);
  top: 50%;
  left: 50%;
  width: 2rem;
  height: 3px;
  background-color: grey;
}

.info-containers .plant-info-container .plant-info-window .btn-close .line-container .line:nth-child(2) {
  transform: translate(-50%, -50%) rotate(45deg);
}

.info-containers .plant-info-container .plant-info-window .plant-image-container {
  width: 50%;
  height: 100%;
  position: relative;
  overflow: hidden;
  opacity: 1;
  transition: 0.5s all ease;
}

.info-containers .plant-info-container .plant-info-window .plant-image-container .plant-image-carousel {
  position: absolute;
  -webkit-animation: 10s plant-image-container-animation infinite;
          animation: 10s plant-image-container-animation infinite;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  height: 100%;
  width: 400%;
}

.info-containers .plant-info-container .plant-info-window .plant-image-container .plant-image-carousel .plant-image {
  width: calc(100% / 4);
  height: 100%;
  background-position: center;
  background-size: cover;
}

@-webkit-keyframes plant-image-container-animation {
  0% {
    left: 0;
  }
  25% {
    left: 0;
  }
  30% {
    left: -100%;
  }
  60% {
    left: -100%;
  }
  65% {
    left: -200%;
  }
  95% {
    left: -200%;
  }
  100% {
    left: -300%;
  }
}

@keyframes plant-image-container-animation {
  0% {
    left: 0;
  }
  25% {
    left: 0;
  }
  30% {
    left: -100%;
  }
  60% {
    left: -100%;
  }
  65% {
    left: -200%;
  }
  95% {
    left: -200%;
  }
  100% {
    left: -300%;
  }
}

.info-containers .plant-info-container .plant-info-window .plant-text {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 50%;
  height: 100%;
  padding: 1rem;
  padding-right: 0;
  text-align: justify;
  overflow: hidden;
  transition: 0.5s all ease;
}

.info-containers .plant-info-container .plant-info-window .plant-text p {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 20;
  -webkit-box-orient: vertical;
  width: 100%;
}

.info-containers .plant-info-container .plant-info-window .plant-text .plant-heading {
  margin-bottom: 2rem;
}

.info-containers .plant-info-container .plant-info-window .plant-text .btn-read-more {
  border: 2px solid grey;
  padding: 1rem 2rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  cursor: pointer;
  color: grey;
  font-weight: bold;
  margin-top: 2rem;
}

.info-containers .plant-info-container .plant-info-window-clicked .plant-image-container {
  width: 0%;
  height: 0%;
  opacity: 0;
}

.info-containers .plant-info-container .plant-info-window-clicked .plant-text {
  width: 100%;
}

.info-containers .plant-info-container-clicked {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 3;
}
